<template>
    <div>
        <div id="sangong">
            <div class="game-container">
                <div class="game-table">
                    <div class="top">
                        <div class="btn">
                            <img src="./../../../assets/圆角矩形1拷贝@2x.png">
                            <img src="./../../../assets/-@2x.png">
                            <img src="./../../../assets/矩形2拷贝@2x.png">
                        </div>
                        <div class="fapai"><img src="./../../../assets/发牌按钮@2x.png"><scan>{{ countDown }}</scan></div>
                    </div>
                    <div class="banker">
                        <div class="banker-card">
                            <img v-bind:src="this.SANGONG.sanGong.deal.deal.banker[0]">
                            <img v-bind:src="bankerCard2" >
                            <img v-bind:src="bankerCard3">
                        </div>
                        <div class="banker-info">
                            <div class="banker-head">
                                <img src="./../../../assets/图层15@2x.png">
                            </div>
                            <div class="banker-name">
                                <p class="banker-nickname">nickname</p>
                                <p>金币 2001</p>
                            </div>
                        </div>
                    </div>
                    <div class="player">
                        <div class="player-box">
                            <div class="player-card">
                                <img v-bind:src="player1Card0">
                                <img v-bind:src="player1Card1">
                                <img v-bind:src="player1Card2">
                            </div>
                            <div class="player-info">
                                <div class="player-head">
                                    <img src="./../../../assets/图层15@2x.png">
                                </div>
                                <div class="player-name">
                                    <p class="player-nickname">nickname</p>
                                    <p>gold 2000</p>
                                </div>
                            </div>
                        </div>
                        <div class="player-box">
                            <div class="player-card">
                                <img v-bind:src="player2Card0">
                                <img v-bind:src="player2Card1">
                                <img v-bind:src="player2Card2">
                            </div>
                            <div class="player-info">
                                <div class="player-head">
                                    <img src="./../../../assets/图层15@2x.png">
                                </div>
                                <div class="player-name">
                                    <p class="player-nickname">nickname</p>
                                    <p>金币 1999</p>
                                </div>
                            </div>
                        </div>
                        <div class="player-box">
                            <div class="player-card">
                                <img v-bind:src="player3Card0">
                                <img v-bind:src="player3Card1">
                                <img v-bind:src="player3Card2">
                            </div>
                            <div class="player-info">
                                <div class="player-head">
                                    <img src="./../../../assets/图层15@2x.png">
                                </div>
                                <div class="player-name">
                                    <p class="player-nickname">nickname</p>
                                    <p>金币 1998</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="bets">
                        <div class="bets-title">庄对牌以上</div>
                        <div class="row col-name">
                            <div class="td">闲1</div>
                            <div class="td">闲2</div>
                            <div class="td">闲3</div>
                        </div>
                        <div class="row col-type">
                            <div class="td">对牌以上</div>
                            <div class="td">对牌以上</div>
                            <div class="td">对牌以上</div>
                        </div>
                        <div class="row">
                            <div class="td" value="player1" @click="bat($event)">
                                <p class="name" value="sanGong">三公</p>
                                <p>1:{{player1SanGong}}</p>
                            </div>
                            <div class="td" value="player2" @click="bat($event)">
                                <p class="name" value="sanGong">三公</p>
                                <p>1:{{player2SanGong}}</p>
                            </div>
                            <div class="td player1SanGong">
                                <p>三公</p>
                                <p>1:{{player3SanGong}}</p>
                            </div>
                        </div>
                        <div class="row">
                            <div class="td">
                                <p>赢</p>
                                <p>1:{{player1Win}}</p>
                            </div>
                            <div class="td">
                                <p>赢</p>
                                <p>1:{{player2Win}}</p>
                            </div>
                            <div class="td">
                                <p>赢</p>
                                <p>1:{{player3Win}}</p>
                            </div>
                        </div>
                        <div class="row">
                            <div class="td">
                                <p>和</p>
                                <p>1:{{player1Peace}}</p>
                            </div>
                            <div class="td">
                                <p>和</p>
                                <p>1:{{player2Peace}}</p>
                            </div>
                            <div class="td">
                                <p>和</p>
                                <p>1:{{player3Peace}}</p>
                            </div>
                        </div>
                        <div class="row">
                            <div class="td">
                                <p>输</p>
                                <p>1:{{player1Lose}}</p>
                            </div>
                            <div class="td">
                                <p>输</p>
                                <p>1:{{player2Lose}}</p>
                            </div>
                            <div class="td active">
                                <p>输</p>
                                <p>1:{{player3Lose}}</p>
                            </div>
                        </div>
                        <div class="row row-chip">
                            <div class="td">{{jetton0}}</div>
                            <div class="td">{{jetton1}}</div>
                            <div class="td">{{jetton2}}</div>
                            <div class="td">{{jetton3}}</div>
                            <div class="td">{{jetton4}}</div>
                            <div class="td">{{jetton5}}</div>
                            <div class="td">{{jetton6}}</div>
                        </div>
                        <div class="row user-chip">
                            <div class="td">自定义下注:</div>
                            <div class="td">{{limit}}</div>
                            <div class="td user-td">剩余金币:</div>
                            <div class="td user-td">{{remaining}}</div>
                        </div>
                    </div>
                    <mt-button class="bets-btn" size="large" @click ="countDownf">下注</mt-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
console.log("GAME_SANGONG_SANGONG_VUE");

    export default {
        data(){
            return {
                player1SanGong: this.SANGONG.sanGong.desk.percent[0].odds,
                player1Win: this.SANGONG.sanGong.desk.percent[1].odds,
                player1Peace: this.SANGONG.sanGong.desk.percent[2].odds,
                player1Lose:this.SANGONG.sanGong.desk.percent[3].odds,
                player2SanGong: this.SANGONG.sanGong.desk.percent[9].odds,
                player2Win: this.SANGONG.sanGong.desk.percent[10].odds,
                player2Peace: this.SANGONG.sanGong.desk.percent[11].odds,
                player2Lose:this.SANGONG.sanGong.desk.percent[12].odds,
                player3SanGong: this.SANGONG.sanGong.desk.percent[18].odds,
                player3Win: this.SANGONG.sanGong.desk.percent[19].odds,
                player3Peace: this.SANGONG.sanGong.desk.percent[20].odds,
                player3Lose:this.SANGONG.sanGong.desk.percent[21].odds,

                jetton0:this.SANGONG.sanGong.desk.jetton[0],
                jetton1:this.SANGONG.sanGong.desk.jetton[1],
                jetton2:this.SANGONG.sanGong.desk.jetton[2],
                jetton3:this.SANGONG.sanGong.desk.jetton[3],
                jetton4:this.SANGONG.sanGong.desk.jetton[4],
                jetton5:this.SANGONG.sanGong.desk.jetton[5],           
                limit:this.SANGONG.sanGong.desk.limit.gold,
                remaining:this.GLOBAL.userInfo.gold,
                countDown:this.SANGONG.sanGong.desk.countDown,

                bankerCard0:this.SANGONG.sanGong.deal.deal.banker[0],
                bankerCard1:this.SANGONG.sanGong.deal.deal.banker[1],
                bankerCard2:this.SANGONG.sanGong.deal.deal.banker[2],
                player1Card0:this.SANGONG.sanGong.deal.deal.player1[0],
                player1Card1:this.SANGONG.sanGong.deal.deal.player1[1],
                player1Card2:this.SANGONG.sanGong.deal.deal.player1[2],
                player2Card0:this.SANGONG.sanGong.deal.deal.player2[0],
                player2Card1:this.SANGONG.sanGong.deal.deal.player2[1],
                player2Card2:this.SANGONG.sanGong.deal.deal.player2[2],
                player3Card0:this.SANGONG.sanGong.deal.deal.player3[0],
                player3Card1:this.SANGONG.sanGong.deal.deal.player3[1],
                player3Card2:this.SANGONG.sanGong.deal.deal.player3[2],
           }
        },
        methods:{
            countDownf: function() {
            },
            bat: function(event){
                 var clickPlayer=event.currentTarget.getAttribute('value');
                 var clickType=event.currentTarget.firstChild.getAttribute('value');
                 if(event.currentTarget.style.background == 'red'){
                     event.currentTarget.style.background='blue';
                     var result=this.addJson(clickPlayer,clickType);
                 }
                 else{
                     event.currentTarget.style.background ='red';
                     var result=this.delJson(clickPlayer,clickType);
                 }
                // document.getElementsByClassName("player1SanGong").
            }   
        },
        mounted: function(){
            // this.$store.state.isPageLogin = true;
            document.getElementById('sangong').style.height = window.screen.height + 'px';
        },
        beforeCreate:function() {
                document.getElementsByTagName("body")[0].className="mg0";
                document.getElementsByTagName("html")[0].className="mg0";
                var that = this;
                var timer = setInterval(function(){
                    // console.log(that.GLOBAL.sanGong.countDown);
                    if(that.SANGONG.sanGong.desk.countDown>0){
                        that.countDown = --that.SANGONG.sanGong.desk.countDown;
                    }
                    if(that.countDown <= -5){

                        clearInterval(timer);
                        console.log("GAME Start");
                        //var data = //拼接字符串把用户的下注信息发送过去；
                // that.GLOBAL.socketHand.send('{"type":"play","room":"'+that.SANGONG.sanGong.room+'"}');
                bankerCard0=this.SANGONG.sanGong.deal.deal.banker[0],
                bankerCard1=this.SANGONG.sanGong.deal.deal.banker[1],
                bankerCard2=this.SANGONG.sanGong.deal.deal.banker[2],
                player1Card0=this.SANGONG.sanGong.deal.deal.player1[0],
                player1Card1=this.SANGONG.sanGong.deal.deal.player1[1],
                player1Card2=this.SANGONG.sanGong.deal.deal.player1[2],
                player2Card0=this.SANGONG.sanGong.deal.deal.player2[0],
                player2Card1=this.SANGONG.sanGong.deal.deal.player2[1],
                player2Card2=this.SANGONG.sanGong.deal.deal.player2[2],
                player3Card0=this.SANGONG.sanGong.deal.deal.player3[0],
                player3Card1=this.SANGONG.sanGong.deal.deal.player3[1],
                player3Card2=this.SANGONG.sanGong.deal.deal.player3[2],

                        that.$forceUpdate();
                    }
                }, 1000);
        },

        beforeDestroy: function() {
            document.getElementsByTagName("html")[0].removeAttribute("class","mg0");
            document.body.removeAttribute("class","mg0");
        },

    }
    
</script>

<style scoped>
#sangong{
    background: url('./../../../assets/色彩平衡1@2x.png') no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
    overflow-y: auto;
}
.game-container{
    padding: 10px;
}
.top{
    display: flex;
    justify-content: space-between;
}
.btn{
    display: flex;
    flex-wrap: nowrap;
}
.btn img{
    height: 35px;
    width: 35px;
    margin-left: 10px;
    margin-top: 2.5px;
}
.fapai img{
    height: 40px;
}
.fapai scan{
    color:white;
}
.banker{
    margin-top: 10px;
    padding: 0 30px;
    display: flex;
    flex-wrap: nowrap;
}
.banker-card{
    position: relative;
    height: 60px;
    width: 90px;
    padding-top: 10px;
}
.banker-card img{
    height: 60px;
    width: 40px;
    position: absolute;
}
.banker-card img:nth-child(2){
    left: 20px;
}
.banker-card img:nth-child(3){
    left: 40px;
}
.banker-info{
    margin-top: 2.5px;
    height: 60px;
    padding: 5px;
    border: 1px solid #AAA3E5;
    display: flex;
    flex-wrap: nowrap;
    border-radius: 5px;
}
.banker-head{
    height: 60px;
    width: 60px;
}
.banker-head img{
    height: 100%;
    width: 100%;
}
.banker-name{
    text-align: left;
    padding: 5px;
    font-size: 14px;
    color: #E7E3F6;
    line-height: 25px;
}
.banker-nickname{
    max-width: 70px;
    overflow: hidden;
}
.banker-name p:last-child{
    font-size: 16px;
}
.player{
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}
.player-box{
    width: 30%;
    min-width: 90px;
    overflow: hidden;
}
.player-card{
    position: relative;
    height: 60px;
    width: 90px;
}
.player-card img{
     height: 60px;
    width: 40px;
    position: absolute;
}
.player-card img:nth-child(2){
    left: 20px;
}
.player-card img:nth-child(3){
    left: 40px;
}
.player-info{
    height: 30px;
    padding: 5px 0;
    display: flex;
    justify-content: space-between;
}
.player-head{
    height: 30px;
    width: 30px;
}
.player-head img{
    height: 30px;
    width: 30px;
}
.player-name{
     text-align: left;
    font-size: 12px;
    color: #E7E3F6;
    line-height: 16px;
    margin-left: 3px;
}
.bets-title{
    color: #fff;
    font-size: 12px;
    padding: 5px;
    background-color: rgba(255, 255, 255, .1)
}
.row{
    display: flex;
    flex-wrap: nowrap;
}
.td{
    color: #ECE4F1;
    border:1px solid #7D4ED1;
    width: 33%;
    font-size: 12px;
    line-height: 20px;
    
}
.row .td:nth-child(2){
    border-left: none;
    border-right: none;
}
.col-name .td{
    line-height: 26px;
    border: 1px solid #BACACD;
    background-color: #01C11C;
}
.col-type .td{
    line-height: 24px;
}
.row-chip .td{
    line-height: 30px;
}
.user-chip .td{
    line-height: 40px;
}
.user-td{
    color: #DCBD3A;
}
.bets-btn{
    font-size: 14px;
    color: #ECE4F1;
    margin-top: 10px;
    background-color: #BB096E;
}
.active{
    background-color: #BB096E;
    border-color: #D3B7DB;
}
</style>


