exports.install = function (Vue, options) {
    Vue.prototype.SanGongEnter = function ( desk ){
        console.log("playerid: "+this.GLOBAL.userInfo.id+" enter desk: "+desk);
        var data = '{"type":"enter"'
                    +',"room":'+'"'+desk+'"'
                    +',"id":'+'"'+this.GLOBAL.userInfo.id
                    +'"}';
        this.GLOBAL.socketHand.send(data);
    }
    Vue.prototype.SanGongPlay = function ( desk ){
        console.log("playerid: "+this.GLOBAL.userInfo.id+" play in desk: "+desk);
        var data = '{"type":"play"'+',"room":'+desk+'}';
        this.GLOBAL.socketHand.send(data);
    }
}


